import React, {useState, useEffect} from 'react';
import { useSelector } from 'react-redux';
import { Row, Col, Modal, Button} from 'react-bootstrap';
import { useHistory } from 'react-router-dom';

function Dairy(props) {

    const orgID = props.orgID;
    const orgLayouts = useSelector(state => state.layouts);
    const days = orgLayouts.timetableDays; 
    const dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];

    const times = ['00:00', '00:30', '01:00', '01:30', '02:00', '02:30', '03:00', '03:30', '04:00', '04:30', '05:00', '05:30', '06:00', '06:30', '07:00', '07:30', '08:00', '08:30', '09:00', 
                    '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30', '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30',
                    '19:00', '19:30', '20:00', '20:30', '21:00', '21:30', '22:00', '22:30', '23:00', '23:30'];
    const history = useHistory();

    const [settings, setSettings] = useState({
        slotClass: '',
        dayClass: '',
        times: [],
        startTime: '',
        finishTime: '',
        interval: '',
        noOfDays: 0,
        thisWeekbegin: new Date(),
        currentWeek: new Date()
    });

    useEffect(() => {
        setup();
    }, []);

    const [modal, setModal] = useState({
        open: false,
        heading: '',
        message: '',
    });

    function handleModalClose() {
        setModal(prevState => {
            return {...prevState,
            open: false
        }
    });
    }


    function setup() {

        let totalDays = 0;
        for(const day of days) {
            if(day) {
                totalDays++;
            }
        }

        setSettings(prevState => {
            return {...prevState, slotClass: 'session' + totalDays + '-slot timetable-layout', dayClass: 'session' + totalDays + '-days timetable-layout', startTime: orgLayouts.startTime, 
                    finishTime: orgLayouts.finishTime, interval: orgLayouts.timeInterval}
        });

        BuildTimes(orgLayouts.startTime, orgLayouts.finishTime, orgLayouts.timeInterval);

        FindWeekBegin();
    }

    function FindWeekBegin() {

        let dayIndex;
        for(const [index, day] of days.entries()) {
            if(day) {
                dayIndex = index;
                break;
            }
        }

        let weekbg = new Date();

        while (weekbg.getDay() != dayIndex) {
            weekbg = minusDay(weekbg);
        }
        setSettings(prevState => {
            return {...prevState, thisWeekbegin: weekbg, currentWeek: weekbg}
        });
    }

    function minusDay(date) {
        var d = new Date();
        d.setDate(date.getDate() - 1);
        return d;
    }

    function BuildTimes(sTime, fTime, interval) {

        const ti = [];
        if(sTime !== 'Start Time' && fTime !== 'Finish Time' && interval !== 0) {
            
            const startTime = sTime.split(':');
            const finishTime = fTime.split(':');

            var day = new Date(0,0,0, startTime[0], startTime[1]);
            const finish = new Date(0,0,0, finishTime[0], finishTime[1]);

            while(day.getTime() < finish.getTime()) {

                ti.push(formatString(day.getHours()) + ':' + formatString(day.getMinutes()));
                day = addMinutes(day, interval);
            }

            setSettings(prevState => {
                return {...prevState, times: ti}
            });
        }
        
    }

    function addMinutes(dt, minutes) {
        return new Date(dt.getTime() + minutes*60000);
    }

    function formatString(time) {
        
        if(time.toString().length == 1) {
            return '0' + time;
        } else {
            return time;
        }
    }

    function handleAdvancedWeek() {

        const days = settings.noOfDays + 7;
        
        var newDate = new Date();
        newDate.setDate(settings.thisWeekbegin.getDate() + days);

        setSettings(prevState => {
            return {...prevState, noOfDays: days, currentWeek: newDate}
        });
    }

    function handleGoBackWeek() {

        const days = settings.noOfDays - 7;
        
        var newDate = new Date();
        newDate.setDate(settings.thisWeekbegin.getDate() + days);

        if(newDate < settings.thisWeekbegin) {
            setModal({heading: props.roomName, message: 'You cannot go back previous from this week', open: true});
        } else {
            setSettings(prevState => {
                return {...prevState, noOfDays: days, currentWeek: newDate}
            });
        }
    }

    function handleBookClick(event) {
        
        const { id } = event.target;
        console.log(id);
        history.push('/org/' + orgID + '/book');
    }

    return (
        <div>
        <Row>
            <Col><strong>
                <div className='centred'>Week Beginning: {settings.currentWeek.toLocaleDateString()}</div>
                </strong>
            </Col>
            <Col>
                <div className='side-by-side-R'>
                    <Button variant='primary' onClick={handleGoBackWeek}>-</Button>
                    <Button variant='primary' onClick={handleAdvancedWeek}>+</Button>
                </div>
            </Col>
        </Row>
        <Row>
            <table className='timetable-layout' width='100%' border='1px'>
            <thead>
                <tr>
                    <td className={settings.slotClass}>
                        Session
                    </td>
                    {days.map((day, index) => {
                        if(day) {
                            return <td className={settings.dayClass} key={index} >{dayNames[index]}</td>
                        }
                    })}
                </tr>
            </thead>
            <tbody>
                {settings.times.map((time, index) => {
                    return(<tr key={index}>
                            <td className={settings.slotClass}>{time}</td>
                            {days.map((day, index) => {
                                if(day) {
                                    return <td className='timetable-layout slot' key={index} id={dayNames[index] + '-' + time} onClick={handleBookClick}>Book</td>
                                }
                            })
                            }
                            </tr>)
                })}
                </tbody>
            </table>
        </Row>

        <Modal show={modal.open} onHide={handleModalClose}>
                <Modal.Header closeButton>
                <Modal.Title>{modal.heading}</Modal.Title>
                </Modal.Header>
                <Modal.Body>{modal.message}</Modal.Body>
                <Modal.Footer>
                <Button variant="primary" onClick={handleModalClose}>
                    Close
                </Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default Dairy

